import { useEffect, useState } from "react";

const RT = () => {
  const [data, setData] = useState();
  const cols = ["id", "name", "email"];
  useEffect(() => {
    (async () => {
      const url = "https://jsonplaceholder.typicode.com/users";
      const res = await fetch(url);
      const dt = await res.json();
      setData(dt);
    })();
  }, []);

  return (
    <table>
      <thead>
        <tr>
          <th> ID</th>
          <th> Name</th>
          <th> Email</th>
        </tr>
      </thead>
      <tbody>
        {data &&
          data.map((i, ind) =>
            cols.map((c) => {
              return (
                <tr>
                  <td>{i["id"]}</td>
                  <td>{i["name"]}</td>
                  <td>{i["email"]}</td>
                </tr>
              );
            })
          )}
      </tbody>
    </table>
  );
};
export default RT;
